.columns {
  width: 100%;
}

.task{
  background-color: blue;
}
.buttoDiv{
  margin: 3%;
  display: flex;
  justify-content: space-between;
}

.listName{
  background-color: rgb(201, 39, 39);
  color: white;
}
.container {
  height: 2em;
  width: 100%;  
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.input {
  border: 1px solid #000000; 
  background-color: #FFFFFF;
  border-radius: 10px;
  margin: 10px;   
  padding: 6px;
  font-weight: normal;
  flex-grow: 1;
}

.inputList{
  border: 1px solid #000000; 
  background-color: #FFFFFF;
  border-radius: 10px;
  margin: 5px;   
  padding: 6px;
  font-weight: normal;
  flex-grow: 1;
}

.addlist {
  font-size: 15px;
  margin: 10px;
  /* padding: 5px; */
  border-radius: 5px;
  color: white;
}
.addlist:hover {
  background-color: lemonchiffon;
}
.progressInput{
  width: 30%;    
}

.progressInput::placeholder {
  color: white;  
}

.addListButton{
  font-size: 15px;
  font-weight: bold;
  margin: 10px;
  /* padding: 5px; */
  border-radius: 5px;
  color: black;
}

.dueDateField{ 
 display: flex;
 justify-content: space-between;
 cursor: pointer;
}

.dueDateFieldInput{ 
  min-width: 100px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
 }

.inputTaskName {
  border: 1px solid #000000; 
  background-color: #EDF5F9;
  border-radius: 4px;
  margin: 2px;   
  padding: 6px;
  font-weight: normal;
  flex-grow: 1;
}


.clean {
  font-size: 100%;
  font-family: inherit;
  border: 0;
  background-color: inherit;
  font-weight: inherit;
  outline: none;
}

.taskProgress{
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.taskProgressButton{

  cursor: pointer;
  width: 100%;
  height: 100%;
  font-size: 100%;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  background-color: inherit;
  font-weight: inherit;
  outline: none;
}

.tableText{
  cursor: pointer;
  display: flex;
  margin: 2%;
}

.select{
  height: 100%;
  padding: 2%;
  margin: 4%;
}
.pen{
  cursor: pointer;
}

.membersDiv{
  margin: 3%;
  display: flex;
  /* flex-wrap: wrap; */
  /* justify-content: space-between; */
}

.membersContainer {
  height: auto;
  width: 100%;  
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.daylyProgress{
  width: 100%;    
  display: flex;
  justify-content: center;
}

.header{
  font-weight: bold;
}